.services-container {
  background: #f4f4f4;
  padding: 3%;
  padding-bottom: 30px;

  .card-container {
    display: flex;
    // align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    row-gap: 20px;
    column-gap: 20px;
    padding: 0 5%;

    .ant-col {
      height: inherit;
    }
  }

  .label {
    background: dimgray;
    margin-bottom: 10px;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 6% 0;
    h1,
    h3 {
      margin: 0;
      color: bisque;
      font-size: 30px;
      border-bottom: 1px solid;
      border-color: coral;
      font-family: cursive;
    }

    h3 {
      font-size: 20px;
      border-bottom: transparent;
    }
  }

  .ant-col {
    height: fit-content;
  }

  @media screen and (max-width: 899px) {
    .label {
      h1,
      h3 {
        margin: 0;
        color: bisque;
        font-size: 30px;
        border-bottom: 1px solid;
        border-color: coral;
        font-family: cursive;
      }

      h3 {
        font-size: 14px;
        border-bottom: transparent;
      }
    }
  }
}
